.about-container {
  padding-top: 10vh;
  width: 100%;
  min-height: 80vh;
  height: auto;
  background: -webkit-gradient(
      linear,
      left top, left bottom,
      from(rgba(255, 255, 255, 0.97)),
      to(rgba(255, 255, 255, 0.97))
    ),
    url("	https://s3.us-east-2.amazonaws.com/hassle-free-beats-untagged-audio/MonitoringBoard.jpeg");
  background: -webkit-linear-gradient(
      rgba(255, 255, 255, 0.97),
      rgba(255, 255, 255, 0.97)
    ),
    url("	https://s3.us-east-2.amazonaws.com/hassle-free-beats-untagged-audio/MonitoringBoard.jpeg");
  background: -o-linear-gradient(
      rgba(255, 255, 255, 0.97),
      rgba(255, 255, 255, 0.97)
    ),
    url("	https://s3.us-east-2.amazonaws.com/hassle-free-beats-untagged-audio/MonitoringBoard.jpeg");
  background: linear-gradient(
      rgba(255, 255, 255, 0.97),
      rgba(255, 255, 255, 0.97)
    ),
    url("	https://s3.us-east-2.amazonaws.com/hassle-free-beats-untagged-audio/MonitoringBoard.jpeg");

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.about-logo {
  width: 60%;
}

.about-blurb {
  width: 75%;
  height: auto;
  line-height: 1.5;
  font-weight: bold;
}

.about-blurb-link {
  text-decoration: none;
  color: #faa916;
}

.about-us-triplet {
  width: 80%;
  margin: 0 5%;
}

.about-blurb-link:hover {
  opacity: 0.5;
}

.additional-info {
  margin: 10vh 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  width: 80%;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.triplet {
  font-size: 5em;
  color: #94021a;
}

.info {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.3)), to(rgba(0, 0, 0, 0.9))),
    url("	https://s3.us-east-2.amazonaws.com/hassle-free-beats-untagged-audio/MonitoringBoard.jpeg");
  background: -webkit-linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.9)),
    url("	https://s3.us-east-2.amazonaws.com/hassle-free-beats-untagged-audio/MonitoringBoard.jpeg");
  background: -o-linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.9)),
    url("	https://s3.us-east-2.amazonaws.com/hassle-free-beats-untagged-audio/MonitoringBoard.jpeg");
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.9)),
    url("	https://s3.us-east-2.amazonaws.com/hassle-free-beats-untagged-audio/MonitoringBoard.jpeg");
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.home-bottom-link {
  text-decoration: none;
  color: #faa916;
}



